<template>
    <div class="home" style="height: 100%;">
        <div class="left">
            <el-menu
                router
                background-color="#324057"
                text-color="#fff"
                default-active="1-4-1" 
                class="el-menu-vertical-demo" 
                @open="handleOpen"
                @close="handleClose"
                :collapse-transition="false"
                :collapse="isCollapse" style="min-height: 100%;">
                <menuTree v-for="item in $store.getters.addRou" :data="item"></menuTree>
            </el-menu>    
        </div>
        <div class="right">
            <Head>
                <div class="mentBtn" @click="bindMentBtn">
                    <i :class="isIcon"></i>
                </div>
            </Head>
            <div class="content_box">  
                <router-view></router-view>
            </div>
        </div>  
    </div>
</template>

<script>
    import Head from '../../components/head'
    import menuTree from '../../components/menuTree'
    export default {
        data() {
            return {
                isCollapse: true,
                isIcon:"el-icon-s-unfold"
            };
        },
        methods: {
            handleOpen(key, keyPath) {
                console.log(key, keyPath);
            },
            handleClose(key, keyPath) {
                console.log(key, keyPath);
            },
            bindMentBtn () {
                if ( this.isCollapse ) {
                    this.isCollapse = false;
                    this.isIcon = 'el-icon-s-fold';
                } else {
                    this.isCollapse = true;
                    
                    this.isIcon = 'el-icon-s-unfold';
                }
                
            }
        },
        mounted() {
            console.log(this.$store.getters.addRou)
        },
        components:{
            Head,
            menuTree
        }
    }
</script>
<style lang="scss" scoped>
    .home {
        display: flex;
        .right {
            flex: 1;
        }
        .mentBtn i {
            font-size: 26px;
        }
    }
    .content_box {
        padding: 20px;
        background-color: #f0f2f5;
        position: relative;
        width: 100%;
        height: 100%;
    }
    .el-menu--dark .el-submenu .el-menu {
        background-color: #1f2d3d;
    }
    
</style>


